@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300;400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@import './shared.css';

/* Design system variables */
:root {
  --primary: rgba(67, 24, 255, 0.9); /* Slightly transparent purple */
  --primary-dark: rgba(107, 78, 255, 0.9); /* Slightly transparent dark mode purple */
  --error: #FF3B3B;
  --success: #05CD99;
  --border-radius-sm: 0.375rem; /* 6px */
  --border-radius-md: 0.5rem; /* 8px */
  --border-radius-lg: 0.75rem; /* 12px */
  --border-radius-xl: 1rem; /* 16px */
  --btn-height: 2.75rem; /* 44px */
  --btn-padding-x: 1rem; /* 16px */
  --btn-padding-y: 0.625rem; /* 10px */
  --btn-font-size: 0.875rem; /* 14px */
  --btn-font-weight: 500;
  --btn-radius: var(--border-radius-lg);
  --btn-icon-size: 1.25rem; /* 20px */
}

/* Prevent scrollbar layout shift */
html {
  scrollbar-gutter: stable;
  font-family: 'Noto Sans', -apple-system, BlinkMacSystemFont, system-ui, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  @apply overflow-x-hidden text-base font-normal;
  overscroll-behavior-y: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 0; /* Reset letter spacing for Noto Sans */
  overflow-y: scroll;
}

/* For Firefox */
html {
  scrollbar-width: thin;
}

/* For Chrome, Edge, and Safari */
html::-webkit-scrollbar {
  width: 8px;
}

html::-webkit-scrollbar-track {
  background: transparent;
}

html::-webkit-scrollbar-thumb {
  background-color: rgba(155, 155, 155, 0.5);
  border-radius: var(--border-radius-lg);
}

/* Dark mode scrollbar adjustments */
html.dark::-webkit-scrollbar-thumb {
  background-color: rgba(100, 100, 100, 0.5);
}

body {
  margin-right: 0 !important;
  padding-right: 0 !important;
  overflow-x: hidden;
  width: 100%;
}

/* Customise scrollbar for modern browsers */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  @apply bg-transparent;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-300 dark:bg-gray-700 rounded-full;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-gray-400 dark:bg-gray-600;
}

/* Hide scrollbar for card details panel while preserving functionality */
.card-details {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
}

.card-details::-webkit-scrollbar {
  display: none; /* Chrome, Safari and Opera */
}

/* Material Icons */
@layer base {
  .material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimiseLegibility;
    font-feature-settings: 'liga';
  }

  /* Default light theme */
  body {
    @apply bg-gray-100 dark:bg-[#111319] text-gray-900 dark:text-white overflow-x-hidden;
    overscroll-behavior-y: none;
  }

  /* Add padding only to dashboard pages */
  .dashboard-page {
    @apply pt-16;
  }

  /* Full-screen pages with no padding top */
  .page-no-padding {
    @apply pt-0;
  }

  /* Dark theme */
  html.dark body {
    @apply bg-[#0B0F19] text-white;
  }

  /* Ensure proper font loading */
  @font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(https://fonts.gstatic.com/s/materialicons/v140/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format('woff2');
  }

  /* Adjust font weights for better rendering */
  h1, h2, h3, h4, h5, h6 {
    @apply font-medium;
    letter-spacing: -0.01em; /* Adjusted for Noto Sans */
  }

  .text-2xl {
    letter-spacing: -0.01em; /* Adjusted for Noto Sans */
  }

  .font-medium {
    @apply tracking-normal; /* Adjusted for Noto Sans */
  }

  /* Typography Scale */
  h1 {
    @apply text-2xl font-semibold;
    letter-spacing: -0.01em; /* Adjusted for Noto Sans */
  }

  h2 {
    @apply text-xl font-semibold;
    letter-spacing: -0.01em; /* Adjusted for Noto Sans */
  }

  h3 {
    @apply text-lg font-semibold;
    letter-spacing: -0.01em; /* Adjusted for Noto Sans */
  }

  h4 {
    @apply text-base font-medium;
  }

  p {
    @apply text-base;
  }

  small {
    @apply text-sm font-medium;
  }

  .text-label {
    @apply text-sm font-medium;
  }

  .text-caption {
    @apply text-xs font-normal;
  }

  /* Stats values */
  .stat-value {
    @apply text-base sm:text-2xl font-semibold;
  }

  .stat-label {
    @apply text-xs font-medium uppercase tracking-normal text-gray-600 dark:text-gray-300;
  }

  /* Card title and content */
  .card-title {
    @apply text-base font-semibold;
  }

  .card-content {
    @apply text-base;
  }

  /* Button text */
  .btn {
    @apply text-base font-medium;
  }

  /* Form elements */
  .form-label {
    @apply text-sm font-medium;
  }

  .form-input {
    @apply text-base;
  }

  .form-hint {
    @apply text-sm font-medium text-gray-600 dark:text-gray-400;
  }

  /* Navigation and menus */
  .nav-item {
    @apply text-base font-medium;
  }

  .menu-item {
    @apply text-base font-medium;
  }

  .dropdown-item {
    @apply text-base font-medium;
  }

  /* Modal elements */
  .modal-title {
    @apply text-xl font-semibold;
  }

  .modal-content {
    @apply text-base;
  }

  /* Card Stats */
  .card-stat-label {
    @apply text-sm font-medium text-gray-700 dark:text-gray-300;
  }

  .card-stat-value {
    @apply text-lg font-semibold text-gray-900 dark:text-white;
  }

  /* Search Input */
  .search-input {
    @apply text-base font-normal placeholder:text-gray-500 dark:placeholder:text-gray-400;
  }

  /* Collection Name */
  .collection-name {
    @apply text-base font-medium;
  }

  /* Sort and Filter Labels */
  .sort-button, 
  .filter-button {
    @apply text-base font-medium;
  }

  /* Card Details */
  .card-details-title {
    @apply text-xl font-semibold;
  }

  .card-details-subtitle {
    @apply text-base font-medium text-gray-700 dark:text-gray-300;
  }

  /* Stats Section */
  .stats-section {
    @apply grid grid-cols-2 sm:grid-cols-4 gap-1.5 sm:gap-3 mb-3 sm:mb-4 px-2 sm:px-4;
  }

  .stat-card {
    @apply bg-white dark:bg-[#1B2131] rounded-lg p-2.5 sm:p-3 shadow-sm flex flex-col;
  }

  .stat-label {
    @apply text-xs font-medium text-gray-600 dark:text-gray-300 mb-0.5;
  }

  .stat-value {
    @apply text-base sm:text-3xl text-gray-900 dark:text-white font-medium;
  }

  /* Profit/Loss Values */
  .profit-value {
    @apply font-semibold;
    &.positive {
      @apply text-green-600 dark:text-green-500;
    }
    &.negative {
      @apply text-red-600 dark:text-red-500;
    }
  }
}

/* Custom styles that are specific to this app */
:root {
  --primary: rgba(67, 24, 255, 0.9); /* Slightly transparent purple */
  --primary-dark: rgba(107, 78, 255, 0.9); /* Slightly transparent dark mode purple */
  --error: #FF3B3B;
  --success: #05CD99;
}

/* App-specific components */
@layer components {
  /* Header Styles */
  .app-header {
    @apply sticky top-0 z-50 bg-gray-100 dark:bg-[#0B0F19] border-b border-gray-200 dark:border-gray-700/50;
  }

  .header-content {
    @apply flex items-center justify-between py-3 px-4 lg:px-6 relative;
  }

  .header-left {
    @apply flex items-center gap-4;
  }

  .header-center {
    @apply hidden lg:flex items-center gap-2 xl:gap-3 justify-center flex-grow px-4;
  }

  .header-right {
    @apply hidden lg:flex items-center gap-2 xl:gap-3;
  }

  /* Header Buttons */
  .header-buttons {
    @apply hidden sm:flex items-center gap-3 ml-auto;
  }

  .header-buttons .btn {
    @apply min-w-[40px] h-[40px] flex items-center justify-center;
  }

  /* Button Styles */
  .btn {
    @apply px-4 py-2.5 rounded-lg transition-all duration-200 font-medium text-sm flex items-center justify-center;
    height: var(--btn-height);
    border-radius: var(--btn-radius) !important;
    font-weight: var(--btn-font-weight);
    font-size: var(--btn-font-size);
    padding: var(--btn-padding-y) var(--btn-padding-x);
    white-space: nowrap;
  }

  .btn span.material-icons {
    @apply inline-block flex-shrink-0;
    font-size: var(--btn-icon-size);
    margin-right: 0.25rem;
  }

  /* Only hide text on desktop for specific side-panel buttons */
  .btn span:not(.material-icons) {
    @apply inline; /* Show text by default */
  }

  /* Mobile menu */
  .mobile-menu {
    @apply fixed inset-x-0 top-[64px] bg-gray-100 dark:bg-[#1B2131] 
           border-t border-gray-200 dark:border-gray-700/50
           shadow-lg z-50 overflow-y-auto max-h-[calc(100vh-64px)];
  }

  .mobile-menu-header {
    @apply flex items-center justify-between p-6 border-b border-gray-200 dark:border-gray-700/50;
  }

  .mobile-menu-content {
    @apply p-6 space-y-4;
  }

  .mobile-menu-section {
    @apply space-y-2;
  }

  .mobile-menu-section-title {
    @apply text-sm font-medium text-gray-500 dark:text-gray-400;
  }

  .mobile-menu-button {
    @apply w-full flex items-center space-x-2 px-4 py-2 
           text-gray-600 dark:text-gray-300 
           bg-gray-100 dark:bg-gray-800 
           rounded-lg 
           hover:bg-gray-200 dark:hover:bg-gray-700 
           transition-colors;
  }

  .mobile-menu-button.active {
    @apply bg-primary text-white;
  }

  /* Prevent body scroll when mobile menu is open */
  .mobile-menu-open {
    @apply overflow-hidden;
  }

  /* Hide desktop elements on mobile */
  @media (max-width: 1023px) {
    .desktop-only {
      @apply hidden;
    }
  }

  /* Hide mobile elements on desktop */
  @media (min-width: 1024px) {
    .mobile-only {
      @apply hidden;
    }
  }

  .mobile-menu-item {
    @apply flex items-center gap-3 w-full px-4 py-3
           text-gray-700 dark:text-gray-200
           hover:bg-gray-50 dark:hover:bg-[#252B3B]
           transition-colors;
  }

  .mobile-menu-item .material-icons {
    @apply text-gray-500 dark:text-gray-400;
  }

  /* Mobile menu button */
  .mobile-menu-button {
    @apply block sm:hidden ml-4 p-2;
  }

  .mobile-menu-button .material-icons {
    @apply inline-block text-xl;
  }

  /* Total Cards Counter */
  .total-cards-counter {
    @apply flex items-center gap-2 px-2 sm:px-4 xl:px-6 py-2;
  }
  
  .total-cards-icon {
    @apply text-gray-600 dark:text-gray-400;
  }
  
  .total-cards-text {
    @apply text-gray-700 dark:text-gray-200 font-medium text-base;
  }

  /* Collection Selector */
  .collection-selector {
    @apply flex items-center gap-2 relative w-full;
  }

  .collection-selector .material-icons {
    @apply inline-block flex-shrink-0;
  }

  /* Collection Dropdown */
  .collection-dropdown {
    @apply absolute top-full left-0 mt-2 
           bg-white dark:bg-[#1B2131] 
           rounded-xl shadow-lg 
           border border-gray-200 dark:border-gray-700/50 
           py-2 z-[150]
           w-[300px] sm:w-[300px];
  }
  
  /* Make dropdown full width on mobile */
  @media (max-width: 639px) {
    .collection-dropdown {
      @apply w-screen left-0 right-0 fixed;
      left: 0;
      width: 100vw;
      border-radius: 0;
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;
      bottom: 0;
      top: auto;
      max-height: 80vh;
      overflow-y: auto;
    }
    
    .collection-item {
      @apply py-3;
    }
  }

  .collection-item {
    @apply flex items-center justify-between w-full px-4 py-2 
           text-gray-900 dark:text-gray-100
           hover:bg-gray-50 dark:hover:bg-[#252B3B] 
           cursor-pointer transition-colors;
  }

  .collection-item span {
    @apply text-gray-900 dark:text-gray-100 break-words whitespace-normal;
  }

  .collection-item .material-icons {
    @apply text-gray-500 dark:text-gray-300;
  }

  .collection-item.text-primary {
    @apply text-purple-700 dark:text-purple-400;
  }

  .collection-item.text-primary span {
    @apply text-purple-700 dark:text-purple-400;
  }

  .collection-divider {
    @apply h-px bg-gray-200 dark:bg-gray-700/50 my-1 mx-4;
  }

  /* Collection Name Button */
  .collection-name {
    @apply flex items-center gap-1.5 xl:gap-2 
           text-gray-900 dark:text-gray-100 font-medium 
           hover:bg-gray-50 dark:hover:bg-[#252B3B] 
           px-2 xl:px-3 py-2 rounded-xl transition-colors
           w-full;
  }

  .collection-name span:not(.material-icons) {
    @apply flex-1 text-gray-900 dark:text-gray-100;
  }

  /* Button Styles */
  .btn-primary {
    @apply bg-purple-100/90 text-purple-700 dark:bg-purple-900/30 dark:text-purple-400 
           hover:bg-purple-200 dark:hover:bg-purple-900/40 border border-purple-200 dark:border-purple-900/30
           flex items-center justify-center gap-2;
  }

  .btn-secondary {
    @apply bg-blue-100 dark:bg-blue-900/20 
           text-blue-600 dark:text-blue-400 
           border-2 border-blue-400 dark:border-blue-500
           hover:bg-blue-200 dark:hover:bg-blue-900/30
           flex items-center justify-center gap-2;
  }

  .btn-tertiary {
    @apply bg-gray-100 dark:bg-gray-800/40 
           text-gray-600 dark:text-gray-400
           hover:bg-gray-200 dark:hover:bg-gray-700/60
           flex items-center justify-center gap-2;
  }

  .btn-danger {
    @apply bg-red-100 dark:bg-red-900/20 
           text-red-600 dark:text-red-400 
           hover:bg-red-200 dark:hover:bg-red-900/30 
           border border-red-200 dark:border-red-900/30
           flex items-center justify-center gap-2;
  }

  /* Delete button */
  .delete-button {
    @apply px-4 py-2.5 rounded-lg 
           bg-red-100 dark:bg-red-900/20
           text-red-600 dark:text-red-400
           border border-red-200 dark:border-red-900/30
           hover:bg-red-200 dark:hover:bg-red-900/30 
           transition-colors flex items-center justify-center gap-2 font-medium;
    height: var(--btn-height);
  }

  /* Custom button styles for specific use cases */
  .btn-custom-purple {
    @apply bg-purple-100 dark:bg-purple-900/20 
           text-purple-600 dark:text-purple-400 
           hover:bg-purple-200 dark:hover:bg-purple-900/30
           flex items-center justify-center gap-2;
  }

  .btn-custom-green {
    @apply bg-green-100 dark:bg-green-900/20 
           text-green-600 dark:text-green-400 
           hover:bg-green-200 dark:hover:bg-green-900/30
           flex items-center justify-center gap-2;
  }

  /* Button sizes */
  .btn-sm {
    @apply px-3 py-1.5 text-xs;
    height: 2.25rem; /* 36px */
  }

  .btn-lg {
    @apply px-5 py-3 text-base;
    height: 3.25rem; /* 52px */
  }

  /* Outline versions */
  .btn-outline-primary {
    @apply bg-transparent border-2 border-primary 
           text-primary hover:bg-primary/10;
  }

  .btn-outline-secondary {
    @apply bg-transparent border-2 border-blue-400 dark:border-blue-500
           text-blue-600 dark:text-blue-400
           hover:bg-blue-100/50 dark:hover:bg-blue-900/10;
  }

  /* For default gray button */
  .btn-default {
    @apply bg-gray-50 dark:bg-[#252B3B]
           text-gray-700 dark:text-gray-300
           border border-gray-200 dark:border-gray-700
           hover:bg-gray-100 dark:hover:bg-[#323B4B]
           flex items-center justify-center gap-2;
  }

  /* Override all old button styles */
  .btn-primary, 
  .sort-button, 
  .view-button, 
  .collection-button,
  button[type="submit"],
  .modal-btn-create,
  .btn-marketing-primary,
  .btn-marketing-secondary,
  .btn-marketing-tertiary {
    height: var(--btn-height);
    border-radius: var(--btn-radius) !important;
  }

  .btn-secondary,
  .modal-btn-cancel {
    height: var(--btn-height);
    border-radius: var(--btn-radius) !important;
  }

  /* Sort dropdown button and Add Card button */
  .sort-button, 
  .view-button, 
  .btn-primary, 
  .btn-secondary, 
  .btn-tertiary,
  .btn-danger,
  .btn-custom-purple,
  .btn-custom-green,
  .btn-default,
  .btn-outline-primary,
  .btn-outline-secondary {
    height: var(--btn-height);
    border-radius: var(--btn-radius) !important;
  }

  /* Stats Section */
  .stats-section {
    @apply grid grid-cols-2 sm:grid-cols-4 gap-1.5 sm:gap-3 mb-3 sm:mb-4 px-2 sm:px-4;
  }

  .stat-card {
    @apply bg-white dark:bg-[#1B2131] rounded-lg p-2.5 sm:p-3 shadow-sm flex flex-col;
  }

  .stat-label {
    @apply text-xs font-medium text-gray-600 dark:text-gray-300 mb-0.5;
  }

  .stat-value {
    @apply text-base sm:text-3xl text-gray-900 dark:text-white font-medium;
  }

  /* This ensures the profit value is colored but keeps the same font size */
  .stat-value.profit-value {
    @apply text-green-500 dark:text-green-400;
  }

  .stat-value.loss-value {
    @apply text-red-500 dark:text-red-400;
  }

  /* Search and Filter Section */
  .search-input {
    @apply w-full px-4 py-3 h-12
           bg-gray-50 dark:bg-[#1B2131]
           border border-gray-200 dark:border-gray-700/50
           rounded-xl
           text-gray-900 dark:text-white
           placeholder-gray-500 dark:placeholder-gray-400
           focus:outline-none focus:ring-2 focus:ring-primary focus:border-transparent;
  }

  .filter-section {
    @apply flex flex-col gap-1.5 px-2 sm:px-4 xl:px-6 py-2;
  }

  /* Sort and View Controls */
  .controls-container {
    @apply flex flex-col sm:flex-row gap-2 w-full mt-1;
  }

  .sort-container, .view-container {
    @apply relative flex-1;
  }

  .controls-right {
    @apply flex flex-row items-center gap-2 mt-2 w-full;
  }

  .sort-button, .view-button {
    @apply flex items-center justify-between w-full px-4 py-3 h-12 rounded-xl 
           bg-gray-50 dark:bg-[#1B2131] 
           border border-gray-200 dark:border-gray-700/50
           text-gray-900 dark:text-gray-100 text-sm
           hover:bg-gray-100 dark:hover:bg-[#252B3B] transition-colors
           relative z-[55];
  }

  .sort-button > div, .view-button > div {
    @apply flex items-center gap-2;
  }

  .sort-button .material-icons, 
  .view-button .material-icons {
    @apply text-gray-500 dark:text-gray-300;
  }

  .sort-dropdown, .view-dropdown {
    @apply absolute top-full left-0 right-0 mt-2 w-full
           bg-gray-50 dark:bg-[#1B2131] rounded-xl shadow-lg 
           border border-gray-200 dark:border-gray-700/50 
           py-2 z-[60];
  }

  .sort-option {
    @apply flex items-center gap-2 px-4 py-2
           text-gray-900 dark:text-gray-100 text-sm
           hover:bg-gray-50 dark:hover:bg-[#252B3B] cursor-pointer;
  }

  .sort-option .material-icons {
    @apply text-primary;
  }

  .sort-option.active {
    @apply bg-gray-50 dark:bg-[#252B3B];
  }

  /* Column Headers */
  .column-headers {
    @apply flex items-center gap-6 px-6 py-3 
           text-sm font-medium text-gray-400;
  }

  .column-header {
    @apply flex items-center gap-1.5 cursor-pointer hover:text-white transition-colors;
  }

  .column-header.active {
    @apply text-white;
  }

  /* Card Grid - Simplified */
  .card-grid {
    @apply grid grid-cols-2 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 
           gap-2 sm:gap-6 p-2 sm:p-4;
  }

  /* Base Card - Single source of truth */
  .card {
    @apply bg-gray-50 dark:bg-[#1B2131] 
           border border-gray-200 dark:border-gray-700/50
           overflow-hidden
           flex flex-col
           transition-all duration-200;
    border-radius: var(--border-radius-lg) !important;
  }

  /* Image Container */
  .card-image {
    @apply w-full p-2 sm:p-4;
  }

  /* Image itself */
  .card-image img {
    @apply w-full h-auto
           rounded-xl sm:rounded-[16px]
           aspect-[3/4.2]
           bg-transparent
           object-contain;
  }

  /* Content Area */
  .card-content {
    @apply px-3 sm:px-6 pb-3 sm:pb-6 flex flex-col gap-2 sm:gap-4;
  }

  /* Card Title Area */
  .card-header {
    @apply space-y-0.5 sm:space-y-1 text-center;
  }

  .card-title {
    @apply text-sm sm:text-lg font-semibold text-gray-900 dark:text-white;
  }

  .card-subtitle {
    @apply text-xs sm:text-base text-gray-600 dark:text-gray-400;
  }

  /* Stats Area */
  .card-stats {
    @apply grid grid-cols-2 gap-2 sm:gap-4;
  }

  .card-stat {
    @apply p-4 rounded-xl bg-gray-50 dark:bg-[#1B2131] border border-gray-200 dark:border-gray-700/50;
    border-radius: var(--border-radius-lg) !important;
  }

  .card-stat-label {
    @apply text-xs sm:text-sm font-medium text-gray-500 dark:text-gray-400;
  }

  .card-stat-value {
    @apply text-sm sm:text-lg font-semibold text-gray-900 dark:text-white;
  }

  /* Profit Section */
  .profit-section {
    @apply mt-1 sm:mt-2 flex items-center justify-between;
  }

  .profit-value {
